import axios from "axios";
import { transformResponse } from ".";
import { IServerResponse, Overwrite, EmailNotification } from "@/shared";
import { IJob } from "../modules/JobsModule";

let serverJobsList: IJob[] = [
  {
    name: "My daily job",
    id: 20,
    repoId: 3,
    emailNotification: EmailNotification.never,
    maxBackups: 100,
    backupLocations: ["C:/2/0", "C:/2/2/0"],
    repeatPattern: "0 0 10 * * *"
  },
  {
    name: "Weekly backup",
    id: 21,
    repoId: 3,
    emailNotification: EmailNotification.onerror,
    maxBackups: 100,
    backupLocations: ["D:/", "C:/0", "C:/4"],
    repeatPattern: "0 0 0 * * 1"
  },
  {
    name: "Important files",
    id: 22,
    repoId: 5,
    emailNotification: EmailNotification.always,
    maxBackups: 110,
    backupLocations: ["D:/0"],
    repeatPattern: "0 0 */10 * * *"
  }
];

export const getJobs = async (): Promise<IServerResponse<{ jobs: IJob[] }>> => {
  await new Promise(resolve => setTimeout(resolve, 200));

  return {
    messages: [],
    payload: {
      jobs: serverJobsList
    }
  };
};

export const createJob = async (
  repo: Overwrite<IJob, { id?: number }>
): Promise<IServerResponse<IJob>> => {
  await new Promise(resolve => setTimeout(resolve, 200));
  const newJob = { ...repo, id: Math.random() };
  serverJobsList = [...serverJobsList, newJob];
  return { messages: [], payload: newJob };
};

export const deleteJob = async (id: number): Promise<IServerResponse> => {
  await new Promise(resolve => setTimeout(resolve, 200));
  serverJobsList = serverJobsList.filter(r => r.id !== id);
  return { messages: [] };
};

export const updateJob = async (job: IJob): Promise<IServerResponse<IJob>> => {
  await new Promise(resolve => setTimeout(resolve, 200));
  serverJobsList = serverJobsList.map(j => (j.id === job.id ? job : j));
  return { messages: [], payload: job };
};
