import { transformResponse } from ".";
import { IServerResponse } from "@/shared";
import { axiosInstance } from "./axios";
import * as path from "path-browserify";

export interface IFileEntry {
  name: string;
  /**
   * Delimited with `/`, no matter which platform.
   */
  fullPath: string;
  folder: boolean;
  writable: boolean;
}

export interface IDirectoryEntryResponseSuccess {
  files: IFileEntry[];
}

interface IDirectoryEntryArgs {
  /**
   * Returns the root if parent is false, which is
   * either `/` on Linux or all drives on Windows.
   *
   * Delimited with `/`, no matter which platform.
   */
  parent: string | false;
}

export const getDirectoryContents = async (
  userId: number,
  args: IDirectoryEntryArgs
): Promise<IServerResponse<IDirectoryEntryResponseSuccess>> => {
  //   const response = await axiosInstance.get<
  //     IServerResponse<IDirectoryEntryResponseSuccess>
  //   >(`/system/fs`);
  //   return transformResponse(response);
  const files: IFileEntry[] = [];
  if (args.parent) {
    let i = 0;
    do {
      const p = path.normalize(`${args.parent}/${i}`);
      files.push({
        folder: i < 3,
        fullPath: p,
        name: path.basename(p),
        writable: Math.random() < 0.3
      });
      ++i;
    } while (i < 10);
  } else {
    files.push(
      {
        name: path.normalize("C://"),
        fullPath: path.normalize("C://"),
        folder: true,
        writable: true
      },
      {
        name: path.normalize("D://"),
        fullPath: path.normalize("D://"),
        folder: true,
        writable: true
      }
    );
  }
  return new Promise(resolve => {
    setTimeout(
      () =>
        resolve({
          messages: [],
          payload: {
            files
          }
        }),
      Math.random() * 100 + 100
    );
  });
};
