

































































import { format } from "date-fns";
import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { settingsModule } from "@/store/modules";
import { Language } from "@/shared";
import { nextCronExecutionDates } from "@/misc/cron";

/**
 * Given as prop
 */
export interface ICalendarEvent {
  title: string;
  details?: string;
  date: Date;
  class: string;
}

/**
 * Actual event rendered
 */
interface IInternalCalendarEvent {
  title: string;
  details: string;
  date: string;
  class: string;
}

const dateToYYYYMMDDString = (d: Date) => format(d, "YYYY-MM-DD");
const dateToHHMMString = (d: Date) => format(d, "HH:mm");

@Component({})
export default class JobExecutionCalendar extends Mixins() {
  @Prop({
    type: String,
    default: "* * * * * *"
  })
  repeatInterval!: string;

  get calendarEvents(): ICalendarEvent[] {
    return nextCronExecutionDates(this.repeatInterval, 100).map(
      (d): ICalendarEvent => ({
        class: "my-event",
        date: d,
        title: this.$t("calendar.job-start").toString()
      })
    );
  }

  calendarStartDate = "";

  mounted() {
    this.calendarStartDate = this.today;
  }
  get eventsMap() {
    const map: { [idx: string]: IInternalCalendarEvent[] | undefined } = {};
    this.calendarEvents.forEach(e => {
      const date = dateToYYYYMMDDString(e.date);
      (map[date] = map[date] || []).push({
        title: e.title,
        details: e.details || dateToHHMMString(e.date),
        date,
        class: e.class
      });
    });
    return map;
  }
  get today() {
    return dateToYYYYMMDDString(new Date());
  }
  resetCalendarStartDate() {
    this.calendarStartDate = this.today;
  }
  get resetCalendarStartDateDisabled() {
    // only compare year and month
    return (
      (this.calendarStartDate || "").substr(0, 7) === this.today.substr(0, 7)
    );
  }
  get locale() {
    return settingsModule.language;
  }
  get weekdayFormat() {
    return this.locale === Language.en
      ? [0, 1, 2, 3, 4, 5, 6] /* start of week = sunday */
      : [1, 2, 3, 4, 5, 6, 0] /* start of week = monday */;
  }
}
